<template>
  <div class="look-template-page">
    <!-- 查看模版 -->
    <el-row>
      <span class="title">
        模版名称：<span class="col-value">{{ templateName }}</span>
      </span>
    </el-row>
    <el-row>
      <span class="title">
        测试月龄：<span class="col-value">{{ ageOfMoon }}</span>
      </span>
    </el-row>
    <AnswerContent :test-arr="TestArr" />
  </div>
</template>
<script>
import AnswerContent from './components/AnswerContent'

import { getGrowDetail } from '@/api/grow-table'
export default {
  components: {
    AnswerContent
  },
  data() {
    return {
      templateName: '',
      ageOfMoon: '',
      TestArr: []
    }
  },
  created() {
    this.getGrowDetail()
  },
  methods: {
    async getGrowDetail() {
      const { data, code } = await getGrowDetail(this.$route.params.id)
      if (code === 20000) {
        if (data && data.growth) {
          this.ageOfMoon = data.growth.month_age
          this.templateName = data.growth.name
          this.TestArr = data.growth.questions
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.look-template-page {
  padding: 50px 55px;
  .title {
    line-height: 35px;
    font-size: 14px;
    font-family: PingFangSC-Medium;
    font-weight: 600;
    color: #17233d;
    letter-spacing: 0;
  }
  .col-value {
    font-weight: normal;
  }
}
</style>
